/* font */
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("MontserratRegular"), local("Montserrat-Regular"),
    url(../fonts/montserrat-regular-webfont.woff2) format("woff2"),
    url(../fonts/montserrat-regular-webfont.woff) format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  font-weight: 600;
  src: local("MontserratSemibold"), local("Montserrat-Semibold"),
    url(../fonts/montserrat-semibold-webfont.woff2) format("woff2"),
    url(../fonts/montserrat-semibold-webfont.woff) format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("MontserratBold"), local("Montserrat-Bold"),
    url(../fonts/montserrat-bold-webfont.woff2) format("woff2"),
    url(../fonts/montserrat-bold-webfont.woff) format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  font-weight: 800;
  src: local("MontserratExtrabold"), local("Montserrat-Extrabold"),
    url(../fonts/montserrat-extrabold-webfont.woff2) format("woff2"),
    url(../fonts/montserrat-extrabold-webfont.woff) format("woff");
}


html {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

:root {
  --black-color: #000;
  --white-color: #fff;
  --orange-color: #F90;
  --gray-color: #cacaca;
  --dark-gray-color: #414141;
  --dark_orange-color: #E1670E;
  --yellow-color: #FFB84D;
  --white-gray-color: #666666;
}


a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: 100%
}

body {
  max-width: 1920px;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
}

ul {
  list-style-type: none;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.btn-reset {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  outline: none;
}



.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site__heading {
  color: var(--black-color);
  font-size: 48px;
  font-weight: 400;
}

.container {
  max-width: 1870px;
  margin: 0 auto;
  padding: 0 50px;
  align-items: center;
  margin-left: 25px;
  margin-right: 7px;
}

@media (max-width: 1560px) {
.container {
  width: 924px;
  margin: 0 auto;
  padding: 0 50px;
}
.footer__text {
  display: none;
}
.header__logo {
  width: 150px;
  height: 48px;
}

.hero__title {
width: 820px;
}

.hero__descr {
  font-size: 20px;
}

}

.flex {
  display: flex;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.btn:focus {
background-color: var(--yellow-color);
}

.btn:hover {
  background-color: var(--yellow-color);
  }
  .btn:active {
    background-color: var(--dark_orange-color);
    }

/* header */
.header {
  width: 100%;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.1);
  border-radius: 0px 0px 32px 32px;
  padding-left: 0px;
  margin-bottom: 48px;
}

.header__container {
  height: 102px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__inner {
  display: flex;
  align-items: center;
}

.nav__list {
  display: flex;
  align-items: center;
}


.nav__item {
  margin-right: 85px;
}



.nav__link {
  font-weight: 600;
  font-size: 18px;
  color: var(--black-color);
  outline: none;
  width: 159px;
}

.nav__link:focus {
outline-offset: 5px;
  outline: 2px solid var(--orange-color);
}

.nav__link:hover {
  border-bottom: 2px solid var(--orange-color);
}

.nav__link:active {
border-bottom: 2px solid var(--dark_orange-color);
color: var(--dark_orange-color);
outline: none;
}

.search {
  width: 26px;
  height: 26px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  fill: var(--black-color);
  stroke: var(--black-color);
}

.search:hover svg, .search:hover circle  {
stroke: var(--orange-color);

}

.search:focus svg {
  outline-offset: 2px;
  outline: 2px solid var(--orange-color);
  }

.search-btn {
  background-color: var(--black-color);
}

.search:active svg, .search:active circle{
  outline: none;
  stroke: var(--dark_orange-color);
}

/* Hero  */
.section-hero {
  padding-bottom: 28px;
}

.hero__content {
  width: 100%;
  border-radius: 32px;
  height: 620px;
  background-image: url("../img/low-angle-photo-of-balconies-2462015\ 1.png");
  background-position: center;
  background-repeat: no-repeat;

}

.hero-content {
  padding: 105px 80px;
}



.hero__title {
  color: var(--white-color);
  max-width: 837px;;
  font-size: 70px;
  font-weight: 800;
  margin-bottom: 24px;
  line-height: 1.2;
}

.hero__descr {
  color: var(--white-color);
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 5.63%;
  max-width: 790px;
}

.hero__btn {
  padding: 25px 39px;
  border-radius: 16px;
  background: var(--orange-color);
  color: var(--white-color);
  text-align: center;
  font-size: 23px;
  font-weight: 700;
}




/* O Hac */
.about-us__section {
  padding-top: 28px;
  padding-bottom: 52px;
}

.about-us-title {
  color: var(--black-color);
  font-size: 48px;
  font-weight: 400;
  margin-bottom: 20px;
}

.about__heading {
  margin-bottom: 19px;
}

.about__text {
  max-width: 1235px;
  margin: 0;
  color: var(--black-color);
  line-height: 200%;
  margin-bottom: 46px;
}

.about-us-content {
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 32px;
}

.left-content {
  display: flex;
  align-items: flex-end;
  max-width: 868px;
  min-height: 622px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  border-radius: 32px;

}

.about-left__inner {
  background: rgba(0, 0, 0, 0.5);
  justify-content: space-between;
  align-items: center;
  padding: 49px;
  border-radius: 32px;
}

.about-left__text {
  color: var(--white-color);
  font-weight: 700;
  line-height: 32px;
  width: 433px;
  margin-right: 160px;
}

.about-left__btn {
  border-radius: 16px;
  border: 3px solid var(--orange-color);
  padding: 18px 38px;
  color: var(--white-color);
}

.about-left__btn:focus{
  background-color: var(--orange-color);
}

.about-left__btn:hover{
  background-color: var(--orange-color);
}

.about-left__btn:active{
  background-color: var(--dark_orange-color);
  border-color: var(--dark_orange-color);
}

.right-content {
  max-width: 870px;
}

.about-right__item {
  padding: 66px 116px 64px 300px;
  border: 1px solid var(--gray-color);
  border-radius: 32px;
  min-height: 150px;
  background-position: left 60px top 63px;
  background-repeat: no-repeat;
}

.about-right__item:first-child {
  margin-bottom: 32px;
}

.about-right__title {
  color: var(--black-color);
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
}

.about-right__text {
  line-height: 32px;
}

/* Этапы */
.stages__section {
  padding-top: 52px;
  padding-bottom: 52px;
}

.stages__heading {
  margin-bottom: 31px;
}

.stages__content {
  justify-content: space-between;
}

.left__content {
  width: 50%;
}

.stages__subtitle {
  color: var(--black-color);
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  margin-bottom: 7px;
}

.stages__text {
  max-width: 758px;
  color: var(--black-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;
  /* 200% */
  margin-bottom: 16px;
}

.right__content {
  width: 49%;
  padding-top: 8px;
}

.stages__redline {
  display: inline-block;
}



.stages-left__btn {
  border-radius: 16px;
  background: var(--orange-color);
  color: var(--white-color);
  align-items: center;
  padding: 21px 38px;
  margin-right: 28px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
}

.stages-right__btn {
  border-radius: 16px;
  border: 1px solid var(--black-color);
  align-items: center;
  color: var(--black-color);
  padding: 20px 37px;
}

.stages-right__btn:focus {
  background-color: var(--white-gray-color);
  color: var(--white-color);
  border-color: var(--white-gray-color);
}

.stages-right__btn:hover {
  background-color: var(--white-gray-color);
  color: var(--white-color);
  border-color: var(--white-gray-color);
}

.stages-right__btn:active {
  background-color: var(--black-color);
  color: var(--white-color);
  border-color: var(--black-color);
}


/* Вопросы */
.section__question {
  padding-top: 51px;
  padding-bottom: 42px;
  margin-bottom: 42px;
}

.question__title {
  margin-bottom: 28px;
}

.question__list {
  padding: 0;
  gap: 32px;
  justify-content: space-between;
}

.question__items {
  border: 1px solid var(--gray-color);
  border-radius: 32px;
  padding: 68px 65px 63px 60px;

}

.question__subtitle {
  max-width: 412px;
  color: var(--black-color);
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 8px;
}

.question__text {
  max-width: 450px;
  color: var(--black-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;
}

/* footer */
.footer {
  background-color: var(--black-color);
}

.footer__left {
  width: 34%;
}

.footer__right {
  width: 66%;
  align-items: center;
  justify-content: space-between;

}

.footer__list {
  margin-bottom: 90px;
  width: 252px;
}

.footer__item {
  color: var(--gray-color);
  font-size: 18px;
  font-weight: 600;
  line-height: 36px;
  /* 200% */
}

.footer__icon svg {
  width: 60px;
  height: 60px;
}

.footer__icon:not(:last-child) {
  margin-right: 22px;
}

.footer__icon:focus {
  outline: 2px solid var(--orange-color);
}

.footer__icon:hover path{
fill: var(--orange-color);
}

.footer__icon:active path{
  outline: none;
  fill: var(--dark_orange-color);
  }

.footer__container {
  align-items: flex-start;
  padding: 50px 50px;
}

.footer__title {
  color: var(--gray-color);
  font-size: 48px;
  font-weight: 400;
  padding-top: 7px;
  margin-bottom: 22px;
}

.footer__form {
  flex-direction: column;

}

.form__input:focus, .form__area:focus {
  background-color: var(--white-gray-color);
}

.form__input:hover, .form__area:hover {
  background-color: var(--white-gray-color);
}



.footer__text {
  color: var(--gray-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;
  /* 200% */
  max-width: 363px;
  padding-top: 3px;
}



.footer__link {
  color: var(--gray-color);
  outline: none;
}

.footer__link:focus {
  outline: 2px solid var(--orange-color);
}

.footer__link:hover {
  color: var(--orange-color);
}

.footer__link:active {
  outline: none;
  color: var(--dark_orange-color);
}

.footer-wrapper {
  min-width: 570px;
  justify-content: space-between;
}

.form__input {
  padding-left: 24px;
  background-color: var(--dark-gray-color);
  height: 54px;
  margin-bottom: 25px;
  border-radius: 16px;
  outline: none;
  color: var(--white-color);
}

.form__input::placeholder {
  color: var(--gray-color);
}

.form__area::placeholder {
  color: var(--gray-color);
}

.form__area {
  padding-left: 24px;
  padding-top: 20px;
  border-radius: 16px;
  resize: none;
  max-width: 100%;
  background-color: var(--dark-gray-color);
  margin-bottom: 35px;
  height: 123px;
  outline: none;
  color: var(--white-color);
}

.form__btn {
  border-radius: 16px;
  background: var(--orange-color);
  padding: 20px 52px;
  margin-right: 16px;
  color: var(--white-color);
  text-align: center;
  font-weight: 700;
}

.checkbox {
  position: absolute;
  opacity: 0;
}

.checbox-text {
  position: relative;
  align-items: center;
  cursor: pointer;
  color: var(--gray-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  /* 200% */
  padding-left: 20px;
}

.checbox-text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 14px;
  height: 14px;
  border: 1px solid var(--gray-color);
}


.checbox-text::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  left: 0;
  top: 0;
  background-image: url("../img/Check.svg");
  background-repeat: no-repeat;
  background-size: 16px;
  opacity: 0;
}


.checkbox:checked+.checbox-text::after {
  opacity: 1;
}
